<template>
  <q-dialog v-model="showDialog" @hide="close">
      <q-card>
        <q-toolbar>
          <q-toolbar-title>Chromatisches Stimmgerät</q-toolbar-title>
          <q-btn flat round dense icon="close" v-close-popup />
        </q-toolbar>

        <q-card-section class="flex flex-center">
          <div class="tuner-device">
            <div class="tuner-container">
              <div class="circle fixed-background-circle"></div>
              <div class="circle moving-circle" :class="isInTune" :style="{ left: centsToPosition }" ></div>
              <div class="circle note-circle">{{ note }}</div>
              <div class="triangle left-triangle" :style="{ display: leftTriangleDisplayStyle }"></div>
              <div class="triangle right-triangle" :style="{ display: rightTriangleDisplayStyle }"></div>
            </div>

            <div class="cents-container">
                <div>{{ displayCents }}</div>
            </div>
          </div>
        </q-card-section>
      </q-card>
    </q-dialog>

</template>

<script>
import { computed } from 'vue'
import { useInstrumentStore } from 'stores/instruments-store';
import { turnTunerOn, turnTunerOff, getTuning } from '@/services/apiServices';

export default {
  name: 'TemplateDialog',
  setup() {
  },
  data() {
    return {
      showDialog: false,
      note: 'E',
      cents: '0',
      intervalId: null,
      instrumentId: null,
    };
  },
  computed: {
    isInTune() {
      return {
        'green-circle': this.cents >= -2 && this.cents <= 2
      };
    },
    displayCents: {
      get() {
        return this.cents + ' ct';
      }
    } ,
    centsToPosition: {
      get() {
        const deviation = Math.min(Math.max(parseInt(this.cents), -10), 10);
        return ((deviation * 5) + 75) + 'px';
      }
    } ,
    leftTriangleDisplayStyle: {
      get() {
        if (this.cents < -2) {
          return 'block';
        } else {
          return 'none';
        }
      },
    },
    rightTriangleDisplayStyle: {
      get() {
        if (this.cents > 2) {
          return 'block';
        } else {
          return 'none';
        }
      }
    }
  },
  methods: {
    startTuner() {
        if (!this.instrumentId)
        {
          return;
        }

        turnTunerOn(this.instrumentId)
        .catch(error => {
          console.error('Error starting tuner:', error);
        });
    },
    stopTuner() {
        if (!this.instrumentId)
        {
          return;
        }

        turnTunerOff(this.instrumentId)
        .catch(error => {
          console.error('Error stopping tuner:', error);
        });
    },
    fetchTuning() {
      if (!this.instrumentId)
      {
        return;
      }

      getTuning(this.instrumentId)
        .then(response => {
          this.note = response.data.note;
          this.cents = response.data.deviation;
        })
        .catch(error => {
          console.error('Error fetching tuning:', error);
        });

    },
    show(instrumentId) {
      this.instrumentId = instrumentId;
      this.showDialog = true;
      this.startTuner();
      this.fetchTuning();

      if (this.instrumentId)
      {
        this.intervalId = setInterval(this.fetchTuning, 500);
      }
    },
    close() {
      if (this.intervalId) {
        clearInterval(this.intervalId);
      }

      this.stopTuner();
      this.showDialog = false
    },
  }
}
</script>

