    width: 150px;
    height: 150px;
    border-radius: 50%;
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 100px;
}

.fixed-background-circle {
    background-color: rgb(232,232,232);
    z-index: 1;
}

.moving-circle {
    background-color: rgb(253,178, 36);
    transition: left 0.1s ease; /* Add transition for smooth movement */
    z-index: 2; 
}

.note-circle {
    background-color: transparent;
    color: black;
    font-family: sans-serif;
    z-index: 3; 
}

.green-circle {
    background-color: green;
}

.triangle {
    width: 0;
    height: 0;
    border-top: 20px solid transparent;
