<template>
  <tuner-dialog ref="tunerDialog"></tuner-dialog>
  <panorama-dialog ref="panoramaDialog"></panorama-dialog>

  <q-card flat class="fixed-width q-mr-xl">
    <q-card-section class="q-pb-xl">
      <span class="text-h5">{{ selectedInstrumentName }}</span>
      <q-fab v-if='!isMuted' class='float-right' color="primary"  icon="keyboard_arrow_left" direction="left">
          <q-fab-action  @click="toggleMute" icon="volume_off">
          </q-fab-action>
          <q-fab-action  @click="openPanoramaSelect" icon="panorama_horizontal">
          </q-fab-action>
          <q-fab-action  v-if='selectedInstrumentHasTuner' @click="openTuner">
            <img class='icon-img' src="@/assets/icons/tuning-fork.png">
          </q-fab-action>
        
      </q-fab>
    </q-card-section>

    <q-card-section v-if='!isMuted' flat class="full-width q-pa-none row no-wrap items-start q-pa-none">
      <div class="col-5">
        <q-card flat>
          <q-card-section class="q-mr-sm">
            <div class="full-width q-mb-md flex flex-center">
              <q-btn :disable='isMuted' round icon="volume_up" color='primary' @click='changeVolume(1)' size="lg"></q-btn>
            </div>
            
            <div class="full-width flex flex-center">
              <q-slider
                :disable="isMuted"
                class='vertical-slider'
                v-model="intVolume"
                track-size="40px"
                thumb-size="0px"
                color="grey-6"
                reverse
                :min="0"
                :max="100"
                vertical
              />
            </div>
            <div class="full-width q-mt-md q-mb-xl flex flex-center">
              <q-btn :disable='isMuted' round icon="volume_down" color='primary' @click='changeVolume(-1)' size="lg"></q-btn>
            </div>
            <div class='full-width flex flex-center'>
              {{  displayVolume }}
            </div>
          </q-card-section>
        </q-card>
      </div>

      <div class="col-7">
        <q-card flat>
          <q-card-section class="q-pl-xs q-pr-md">
            <q-btn padding=''
                      :disable="isMuted"
                      dense
                      no-wrap
                      no-caps size='lg' 
                      class='full-width q-mb-md q-pa-sm' 
                      :class="getPresetButtonClass(index+1)"
                      :label="presets[index]"
                      v-for="(preset, index) in presets" :key="index"
                      @click="selectPreset(index + 1)">
              </q-btn>
          </q-card-section>
        </q-card>
      </div>

    </q-card-section>

    <q-card-section v-if='isMuted' flat class="column items-center" style="margin-top: 100px;">
          <q-btn round icon="volume_off" color='primary' @click='toggleMute' size="xl"></q-btn>
          <div class="q-mt-lg">
            Instrument ist stummgeschaltet
          </div>
    </q-card-section>

  </q-card>
</template>

<script>
import { getInstrumentById, selectInstrumentPreset, toggleInstrumentMute, updateInstrumentVolume } from '@/services/apiServices';
import TunerDialog from '@/components/TunerDialog.vue';  
import PanoramaDialog from '@/components/PanoramaDialog.vue';

export default {
setup() {
},
created() {
  this.updateInstrumentState(this.selectedInstrumentId);
},

name: 'InstrumentControl',
data() {
  return {
    volume: 30,
    isMuted: false,
    currentPreset: null,       // we start with index 0, in the REST api the first element is 1
    presets: [],
  };
},
components:
{
  TunerDialog,
  PanoramaDialog,
},
props: {
  selectedInstrumentName: {
    type: String,
  },
  selectedInstrumentId: {
    type: Number,
  },
  selectedInstrumentHasTuner: {
    type: Boolean,
  }
},
computed: {
  intVolume: {
    get() {
      return this.volume;
    },
    set(value) {
      let volume = parseInt(value, 10);

      this.changeVolume(volume - this.volume);
    }
  },
  displayVolume: {
    get() {
      return "Lautstärke " + this.volume + ' %';
    }
  } 
},
methods: {
  getPresetButtonClass(index) {
      return {
        'bg-primary text-white': index === this.currentPreset
      };
  },
  getMuteButtonClass() {
      return {
        'bg-primary text-white': this.isMuted
      };
  },
  openPanoramaSelect() {
    this.$refs.panoramaDialog.show(this.selectedInstrumentId);
  },
  openTuner() 
  {
    this.$refs.tunerDialog.show(this.selectedInstrumentId);
  },

  updateInstrumentState(selectedInstrumentId) {
    if (!selectedInstrumentId) return;

    getInstrumentById(selectedInstrumentId)
      .then(response => {
        this.isMuted = response.data.is_muted;
        this.volume = response.data.volume;
        this.currentPreset = response.data.selected_preset.id;

        // retrieve reported presets and store names in local array
        this.presets = response.data.presets.slice(0, 6).map(preset => preset.name);
      })
      .catch(error => {
        console.error('Error fetching instrument state:', error);
      });

  },
  toggleMute() {
    toggleInstrumentMute(this.selectedInstrumentId)
      .then(response => {
        this.isMuted = response.data.is_muted
      })
      .catch(error => {
        console.error('Error toggling mute:', error);
      });

  },
  changeVolume(amount) {
    this.volume = Math.max(0, Math.min(100, this.volume + amount));

    // perform REST api call and update state with response
    updateInstrumentVolume(this.selectedInstrumentId, this.volume)
      .then(response => {
        this.volume = response.data.volume
      })
      .catch(error => {
        console.error('Error setting volume:', error);
      });


  },
  selectPreset(presetId) {
    this.currentPreset = presetId;

    selectInstrumentPreset(this.selectedInstrumentId, presetId)
      .then(response => {
        this.currentPreset = response.data.selected_preset.id;
      })
      .catch(error => {
        console.error('Error setting preset:', error);
      });

  }
}
};
</script>

<style scoped>
.icon-img {
  width: 24px;
  height: 24px;
  max-width: 100%;
  max-height: 100%;
}

.fixed-width {
  width: 380px;
}

</style>